<template>
  <v-container class="pt-10">
    <v-sheet class="client-login" width="500">
      <v-form @submit.prevent="handleLogin">
        <div class="form-group">
          <v-text-field
            label="メールアドレス"
            v-model="email"
            type="email"
            placeholder="xxx@example.com"
            outlined
            :error="!!emailError"
            :error-messages="emailError"
            required
          />
        </div>
        <div class="form-group">
          <v-text-field
            label="パスワード"
            v-model="password"
            type="password"
            outlined
            :error="!!passwordError"
            :error-messages="passwordError"
            placeholder=""
            required
          />
        </div>
        <v-btn type="submit" class="login-button" size="large" append-icon="mdi-login">ログイン</v-btn>
      </v-form>
    </v-sheet>
  </v-container>
</template>

<script>
export default {
  name: "ClientLogin",
  data() {
    return {
      email: "",
      password: "",
      emailError: null,
      passwordError: null,
    };
  },
  methods: {
    handleLogin() {
      // バリデーションチェック
      this.emailError = null;
      this.passwordError = null;

      if (!this.validateEmail(this.email)) {
        this.emailError = "正しいメールアドレスを入力してください";
      }

      if (!this.emailError && !this.passwordError) {
        this.$router.push('/user_page'); // ユーザページに遷移
      }
    },
    validateEmail(email) {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    },
  },
};
</script>

